import { APG, ns } from "@underlay/apg"
import { SyntaxNode } from "lezer"

import { parser } from "../grammar/index.js"
import { defaultTypes } from "./stdlib.js"

export const uriPattern = /^[a-z]+:[a-zA-Z0-9-/_.:#]+$/
export const namespacePattern = /[#/]$/

export type ParseState = {
	slice: (node: { from: number; to: number }) => string
	namespaces: Record<string, string>
	schema: Record<string, APG.Type>
	types: Record<string, APG.Type>
	references: { from: number; to: number; key: string }[]
}

export class LintError extends Error {
	constructor(
		readonly from: number,
		readonly to: number,
		readonly value: string,
		message: string
	) {
		super(message)
	}
}

export interface ParseResult {
	schema: APG.Schema
	namespaces: Record<string, string>
}

export function parse(input: string): ParseResult {
	const tree = parser.configure({ strict: true }).parse(input)
	const cursor = tree.cursor()

	if (cursor.name === "Schema") {
		cursor.firstChild()
	} else {
		throw new LintError(cursor.from, cursor.to, "", "Invalid top-level node")
	}

	const state: ParseState = {
		slice: ({ from, to }: { from: number; to: number }) =>
			input.slice(from, to),
		namespaces: {},
		types: { ...defaultTypes },
		schema: {},
		references: [],
	}

	do {
		if (cursor.type.name === "Namespace") {
			const term = cursor.node.getChild("Term")!
			const namespace = state.slice(term)
			if (!uriPattern.test(namespace)) {
				throw new LintError(
					term.from,
					term.to,
					namespace,
					`Invalid URI: URIs must match ${uriPattern.source}`
				)
			} else if (!namespacePattern.test(namespace)) {
				throw new LintError(
					term.from,
					term.to,
					namespace,
					"Invalid namespace: namespaces must end in / or #"
				)
			}

			const identifier = cursor.node.getChild("Prefix")!
			const prefix = state.slice(identifier)
			if (prefix in state.namespaces) {
				const message = `Duplicate namespace: ${prefix}`
				throw new LintError(identifier.from, identifier.to, prefix, message)
			} else {
				state.namespaces[prefix] = namespace
			}
		} else if (cursor.type.name === "Type") {
			const identifier = cursor.node.getChild("TypeName")!
			const expression = cursor.node.getChild("Expression")!
			const type = parseType(state, expression)

			const name = state.slice(identifier)
			if (name in state.types) {
				const { from, to } = identifier
				const message = `Invalid type declaration: type ${name} has already been declared`
				throw new LintError(from, to, name, message)
			} else {
				state.types[name] = type
			}
		} else if (cursor.type.name === "Class") {
			const node = cursor.node.getChild("Term")!
			const term = parseURI(state, node)!
			if (term in state.schema) {
				const { from, to } = node
				const message = `Invalid class declaration: class ${term} has already been declared`
				throw new LintError(from, to, term, message)
			} else {
				const expression = cursor.node.getChild("Expression")!
				state.schema[term] = parseType(state, expression)
			}
		} else if (cursor.type.name === "Edge") {
			const terms = cursor.node.getChildren("Term")
			const names = terms.map((uri) => parseURI(state, uri))

			const expression = cursor.node.getChild("Expression")
			const value = expression && parseType(state, expression)

			const [sourceNode, labelNode, targetNode] = terms
			const [source, label, target] = names
			if (label in state.schema) {
				const { from, to } = labelNode
				const message = `Invalid edge declaration: class ${label} has already been declared`
				throw new LintError(from, to, label, message)
			} else {
				if (!(source in state.schema)) {
					const { from, to } = sourceNode
					state.references.push({ from, to, key: source })
				}

				if (!(target in state.schema)) {
					const { from, to } = targetNode
					state.references.push({ from, to, key: target })
				}

				const components: Record<string, APG.Type> = {
					[ns.source]: APG.reference(source),
					[ns.target]: APG.reference(target),
				}

				if (value !== null) {
					components[ns.value] = value
				}

				state.schema[label] = APG.product(components)
			}
		} else if (cursor.type.name === "List") {
			const node = cursor.node.getChild("Term")!
			const term = parseURI(state, node)!
			if (term in state.schema) {
				const { from, to } = node
				const message = `Invalid list declaration: class ${term} has already been declared`
				throw new LintError(from, to, term, message)
			} else {
				const expression = cursor.node.getChild("Expression")!
				state.schema[term] = APG.coproduct({
					[ns.none]: APG.product({}),
					[ns.some]: APG.product({
						[ns.head]: parseType(state, expression),
						[ns.tail]: APG.reference(term),
					}),
				})
			}
		}
	} while (cursor.nextSibling())

	for (const { from, to, key } of state.references) {
		if (key in state.schema) {
			continue
		} else {
			const message = `Invalid reference: class ${key} is not defined`
			throw new LintError(from, to, key, message)
		}
	}

	return { schema: APG.schema(state.schema), namespaces: state.namespaces }
}

export function parseURI(state: ParseState, node: SyntaxNode): string {
	const value = state.slice(node)
	const index = value.indexOf(":")
	if (index === -1) {
		const { from, to } = node
		const message = `Invalid URI: URIs must be of the form [namespace]:[path]`
		throw new LintError(from, to, value, message)
	}

	const prefix = value.slice(0, index)
	if (prefix in state.namespaces) {
		return state.namespaces[prefix] + value.slice(index + 1)
	} else {
		const { from, to } = node
		const message = `Invalid URI: namespace ${prefix} is not defined`
		throw new LintError(from, to, value, message)
	}
}

// Variable | Optional | Reference  | Uri | Literal | Product | Coproduct
export function parseType(state: ParseState, node: SyntaxNode): APG.Type {
	if (node.name === "Variable") {
		const value = state.slice(node)
		if (value in state.types) {
			return state.types[value]
		} else {
			const { from, to } = node
			throw new LintError(from, to, value, `Type ${value} is not defined`)
		}
	} else if (node.name === "Optional") {
		const expression = node.getChild("Expression")!
		const type = parseType(state, expression)
		return APG.coproduct({ [ns.none]: APG.product({}), [ns.some]: type })
	} else if (node.name === "Reference") {
		const term = node.getChild("Term")!
		const key = parseURI(state, term)
		if (!(key in state.schema)) {
			const { from, to } = term
			state.references.push({ from, to, key })
		}

		return APG.reference(key)
	} else if (node.name === "Uri") {
		return APG.uri()
	} else if (node.name === "Literal") {
		const term = node.getChild("Term")!
		const datatype = parseURI(state, term)
		return APG.literal(datatype)
	} else if (node.name === "Product") {
		const components: Record<string, APG.Type> = {}
		for (const component of node.getChildren("Component")) {
			const term = component.getChild("Term")!

			const key = parseURI(state, term)
			if (key in components) {
				const { to, from } = term
				const message = `Duplicate product component key: ${key}`
				throw new LintError(from, to, key, message)
			} else {
				const expression = component.getChild("Expression")!
				components[key] = parseType(state, expression)
			}
		}

		return APG.product(components)
	} else if (node.name === "Coproduct") {
		const options: Record<string, APG.Type> = {}
		for (const option of node.getChildren("Option")) {
			const term = option.getChild("Term")!
			const key = parseURI(state, term)
			if (key in options) {
				const { from, to } = term
				const message = `Duplicate coproduct option key: ${key}`
				throw new LintError(from, to, key, message)
			} else {
				const expression = option.getChild("Expression")
				if (expression === null) {
					options[key] = APG.product({})
				} else {
					options[key] = parseType(state, expression)
				}
			}
		}
		return APG.coproduct(options)
	} else {
		throw new Error("Unexpected Expression node")
	}
}

export function printSyntax(node: SyntaxNode, prefix: string = "") {
	console.log(`${prefix}- ${node.type.name} ${node.from} ${node.to}`)
	for (let child = node.firstChild; child !== null; child = child.nextSibling) {
		printSyntax(child, prefix + "  ")
	}
}
